<template>
  <div>
    <component :is="'style'">
      :root { --shadow:{{ shadow }}; --white-shadow:{{ whiteShadow }}; --bg:{{
        `url('/image/aspect/${w.Aspect?.toString().padStart(4, '0')}.png')`
      }}; }
    </component>

    <v-card class="d-flex mb-4" style="width: 472px; height: 280px; margin: 0 auto">
      <div class="flex-grow-1 d-flex align-center justify-center" style="position: relative">
        <div v-if="!(activeIndex - 1)">
          <!--  -->
          <img
            :src="`/image/aspect/${w.Aspect?.toString().padStart(4, '0')}.png`"
            alt=""
            class="breathing-image" />

          <!--  -->
          <img
            :src="`/image/aspect/${w.Aspect?.toString().padStart(4, '0')}.png`"
            alt=""
            class="hue-image"
            :style="{
              filter: `hue-rotate(${angle}deg) brightness(${light}%) saturate(${saturate}%)`,
            }" />
        </div>

        <div v-else style="width: 63px; height: 153px">
          <!--  -->
          <div
            style="
              position: absolute;
              left: 50%;
              transform: translateX(-50%) translateY(130px) scaleX(1.5) scaleY(0.4);
              width: 50px;
              height: 50px;
              border-radius: 50%;
              background-color: rgba(0, 0, 0, 0.2);
              filter: blur(10px);
            "></div>

          <!-- -->
          <img
            :src="`/image/aspect/${w.Aspect?.toString().padStart(4, '0')}.png`"
            alt=""
            class="role-breathing-image"
            :style="{ position: 'absolute', transform: wPosition }" />

          <!--  -->
          <img
            :src="`/image/aspect/${w.Aspect?.toString().padStart(4, '0')}.png`"
            alt=""
            :style="{
              position: 'absolute',
              transform: wPosition,
              filter: `hue-rotate(${angle}deg) brightness(${light}%) saturate(${saturate}%)`,
            }" />

          <!--  -->
          <img :src="`/image/${activeIndex - 1}.png`" alt="" style="position: absolute" />
        </div>

        <div
          class="d-flex align-center pa-1 rounded-lg"
          style="
            position: absolute;
            bottom: 10px;
            right: 10px;
            background-color: #fff;
            border: 1px solid #0000001f;
          ">
          <!--  -->
          <v-menu bottom left :close-on-content-click="false">
            <template v-slot:activator="{ on, attrs }">
              <div
                v-bind="attrs"
                v-on="on"
                :style="{
                  width: '18px',
                  height: '18px',
                  cursor: 'pointer',
                  borderRadius: '50%',
                  background: `url(/image/icon/${w.Icon.toString().padStart(5, '0')}.jpg)`,
                  backgroundSize: 'cover',
                }"></div>
            </template>

            <v-card outlined class="pa-2">
              <!--  -->
              <v-item-group
                v-model="activeIndex"
                mandatory
                class="d-flex justify-space-between"
                style="width: 140px">
                <v-item :value="1" v-slot="{ active, toggle }">
                  <div
                    @click="toggle"
                    :style="{
                      width: '24px',
                      height: '24px',
                      cursor: 'pointer',
                      borderRadius: '50%',
                      background: `url('/image/icon/${w.Icon.toString().padStart(5, '0')}.jpg')`,
                      backgroundSize: 'cover',
                    }"></div>
                </v-item>

                <v-item
                  v-for="(n, index) in ['15308', '15310', '15309', '15311']"
                  :key="index"
                  :value="index + 2"
                  v-slot="{ active, toggle }">
                  <div
                    @click="toggle"
                    :style="{
                      width: '24px',
                      height: '24px',
                      cursor: 'pointer',
                      borderRadius: '50%',
                      background: `url(/image/icon/${n}.jpg)`,
                      backgroundSize: 'cover',
                    }"></div>
                </v-item>
              </v-item-group>
            </v-card>
          </v-menu>

          <v-divider class="mx-1" vertical></v-divider>

          <!--  -->
          <v-menu bottom left :close-on-content-click="false" @input="input">
            <template v-slot:activator="{ on, attrs }">
              <v-btn x-small dark icon v-bind="attrs" v-on="on">
                <!-- <v-icon color="#222">mdi-tune</v-icon> -->
                <v-icon color="#555" size="18">mdi-invert-colors</v-icon>
              </v-btn>
            </template>

            <v-card outlined class="pa-2">
              <div id="picker"></div>
            </v-card>
          </v-menu>

          <!--  -->
          <div style="width: 18px; height: 18px">
            <v-swatches
              swatch-size="20"
              v-model="color"
              :swatches="swatches"
              row-length="6"
              shapes="circles"
              show-border
              popover-y="top"
              popover-x="left"
              :close-on-select="false"
              :trigger-style="{ width: '18px', height: '18px' }" />
          </div>
        </div>
      </div>
    </v-card>

    <v-card style="margin: 0 auto; width: 472px">
      <div style="padding: 10px">
        <!--  -->
        <v-card outlined class="d-flex justify-space-between mb-4">
          <v-text-field
            v-model="keyword"
            solo
            dense
            flat
            prepend-inner-icon="mdi-magnify"
            hide-details="false"
            placeholder=".."
            clearable
            color="initial"
            style="font-size: 0.8em"></v-text-field>
        </v-card>

        <!--  -->
        <v-item-group v-model="select" class="mb-2">
          <v-item
            v-for="(item, index) in category"
            :key="index"
            v-if="!(item.type2[0] === 6)"
            :value="item.type2"
            v-slot="{ active, toggle }"
            class="mr-2 mb-2">
            <v-btn
              outlined
              small
              :class="active ? 'grey darken-4 white--text' : ''"
              class="px-1"
              @click="toggle">
              {{ item.name }}
            </v-btn>
          </v-item>
        </v-item-group>

        <!-- / -->
        <div class="list" :style="{ width: isMobile ? '100%' : '452px' }">
          <!--  -->
          <div
            v-for="(item, index) in filterList"
            :key="index"
            @click="put(item)"
            class="item"
            style="cursor: pointer">
            <tooltip :item="item" :category="category" />
          </div>
        </div>
      </div>
    </v-card>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import _ from 'lodash';
import Tooltip from '@/views/enhance/components/tooltip.vue';
import VSwatches from 'vue-swatches';
import iro from '@jaames/iro';

export default {
  components: { Tooltip, VSwatches },

  data() {
    return {
      w: {
        Icon: 29836,
        Aspect: 3764,
        Name: '',
        Type: 8,
        Type2: 3,
        Bullet: 5,
        MaxBullet: 20,
        Bullet_Speed: 12230,
        Delay: 4210,
        Distance: 3030,
        Accuracy: 20,
        Explain: '',
        Sound: 76,
      },

      category: [
        {
          name: '',
          type: 8,
          type2: [2, 8],
        },
        {
          name: '',
          type: 8,
          type2: [3],
        },
        {
          name: '',
          type: 8,
          type2: [4],
        },
        {
          name: '',
          type: 8,
          type2: [7],
        },
        {
          name: '',
          type: 8,
          type2: [1],
        },
        {
          name: '',
          type: 8,
          type2: [6],
        },
      ],

      keyword: '',

      select: [2, 8],

      angle: 0,

      light: 100,

      saturate: 100,

      opacity: 1,

      hueColor: '',

      color: '',

      activeIndex: 1,

      swatches: [['#FFFF8D', '#69F0AE', '#536DFE', '#FF5252', '#E040FB', '']],

      colorPicker: null,
    };
  },

  mounted() {},

  computed: {
    ...mapState('weapon', ['weaponList']),

    shadow() {
      return this.color
        ? `drop-shadow(${this.offset}px 0 0px ${this.color}) drop-shadow(-${this.offset}px 0 0px ${this.color})
      drop-shadow(0 ${this.offset}px 0px ${this.color}) drop-shadow(0 -${this.offset}px 0px ${this.color})`
        : 'drop-shadow(0 0 0 transparent)';
    },
    whiteShadow() {
      return this.color
        ? `drop-shadow(${this.offset}px 0 0px #fff) drop-shadow(-${this.offset}px 0 0px #fff)
      drop-shadow(0 ${this.offset}px 0px #fff) drop-shadow(0 -${this.offset}px 0px #fff)`
        : 'drop-shadow(0 0 0 transparent)';
    },

    offset() {
      if (this.color === '#FFFF8D') {
        return '1';
      } else if (this.color === '#69F0AE' || this.color === '#536DFE') {
        return '2';
      }
      return '3';
    },

    isMobile() {
      return this.$vuetify.breakpoint.xs; // < 600px
    },

    filterList() {
      let list = [];

      // 
      if (!this.select) {
        list = this.weaponList;
      } else {
        list = this.weaponList.filter(item => this.select.includes(item.Type2));
      }

      // 
      if (this.keyword?.trim()) {
        return list.filter(item =>
          _.includes(item.Name.toString().toLowerCase(), this.keyword.toLowerCase())
        );
      }

      return list;
    },

    ericPosition() {
      let x = '0',
        y = '0',
        scale = '0',
        rotation = '0';
      if (this.w.Type2 === 2 || this.w.Type2 === 8) {
        x = '-14';
        y = '42';
        scale = '0.93';
        rotation = '95';
      }
      if (this.w.Type2 === 3) {
        x = '-74';
        y = '41';
        scale = '0.82';
        rotation = '92';
      }
      if (this.w.Type2 === 4) {
        x = '-79';
        y = '23';
        scale = '0.82';
        rotation = '90';
      }
      if (this.w.Type2 === 7) {
        x = '-10';
        y = '45';
        scale = '0.79';
        rotation = '91';
      }
      return `translateX(${x}px) translateY(${y}px) scale(${scale}) rotate(${rotation}deg)`;
    },

    clarePosition() {
      let x = '20',
        y = '0',
        scale = '0',
        rotation = '0';
      if (this.w.Type2 === 2 || this.w.Type2 === 8) {
        x = '-13';
        y = '48';
        scale = '.88';
        rotation = '99';
      }
      if (this.w.Type2 === 3) {
        x = '-78';
        y = '41';
        scale = '.77';
        rotation = '97';
      }
      if (this.w.Type2 === 4) {
        x = '-77';
        y = '22';
        scale = '.80';
        rotation = '95';
      }
      if (this.w.Type2 === 7) {
        x = '-14';
        y = '52';
        scale = '.79';
        rotation = '92';
      }
      return `translateX(${x}px) translateY(${y}px) scale(${scale}) rotate(${rotation}deg)`;
    },

    vlatkoPosition() {
      let x = '0',
        y = '0',
        scale = '0',
        rotation = '0';
      if (this.w.Type2 === 2 || this.w.Type2 === 8) {
        x = '-15';
        y = '26';
        scale = '0.93';
        rotation = '92';
      }
      if (this.w.Type2 === 3) {
        x = '-78';
        y = '31';
        scale = '1';
        rotation = '90';
      }
      if (this.w.Type2 === 4) {
        x = '-77';
        y = '3';
        scale = '1';
        rotation = '90';
      }
      if (this.w.Type2 === 7) {
        x = '-9';
        y = '31';
        scale = '1';
        rotation = '90';
      }
      return `translateX(${x}px) translateY(${y}px) scale(${scale}) rotate(${rotation}deg)`;
    },

    bibianaPosition() {
      let x = '20',
        y = '0',
        scale = '0',
        rotation = '0';
      if (this.w.Type2 === 2 || this.w.Type2 === 8) {
        x = '-17';
        y = '39';
        scale = '.86';
        rotation = '87';
      }
      if (this.w.Type2 === 3) {
        x = '-81';
        y = '40';
        scale = '.77';
        rotation = '87';
      }
      if (this.w.Type2 === 4) {
        x = '-81';
        y = '22';
        scale = '.80';
        rotation = '88';
      }
      if (this.w.Type2 === 7) {
        x = '-14';
        y = '48';
        scale = '.84';
        rotation = '85';
      }
      return `translateX(${x}px) translateY(${y}px) scale(${scale}) rotate(${rotation}deg)`;
    },

    wPosition() {
      if (this.activeIndex === 2) {
        return this.ericPosition;
      }
      if (this.activeIndex === 3) {
        return this.clarePosition;
      }

      if (this.activeIndex === 4) {
        return this.vlatkoPosition;
      }

      if (this.activeIndex === 5) {
        return this.bibianaPosition;
      }
    },
  },

  methods: {
    // 
    put(item) {
      this.w = item;
    },

    input(value) {
      if (value && !this.colorPicker) {
        this.$nextTick().then(() => {
          this.colorPicker = new iro.ColorPicker('#picker', {
            width: 220,
            sliderSize: 20,
            color: { h: 180, s: 100, v: 100 },

            borderWidth: 1,
            borderColor: '#222',
            colorModel: 'customHsv',
            layout: [
              {
                component: iro.ui.Slider,
                options: {
                  sliderType: 'hue',
                },
              },
              {
                component: iro.ui.Slider,
                options: {
                  sliderType: 'saturation',
                },
              },
              {
                component: iro.ui.Slider,
                options: {
                  sliderType: 'value',
                },
              },
            ],
          });

          this.colorPicker.on('color:change', color => {
            if (!color.index) {
              this.angle = color.hue;
              this.saturate = color.saturation;
              this.light = color.value;
            }
          });
        });
      }
    },
  },
};
</script>

<style scoped>
.boshadow-image,
.hue-image,
.breathing-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  user-select: none;
}

/*  */
.breathing-image,
.role-breathing-image {
  animation: bgLight 2s linear infinite;
  user-select: none;
}

@keyframes bgLight {
  0% {
    filter: drop-shadow(0 0 0 transparent);
  }

  10% {
    filter: var(--shadow);
  }

  50% {
    filter: var(--white-shadow);
  }

  90% {
    filter: var(--shadow);
  }

  100% {
    filter: drop-shadow(0 0 0 transparent);
  }
}

.list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(32px, 1fr));
  gap: 10px;
}

.vue-swatches {
  display: initial;
}

::v-deep .vue-swatches__trigger__wrapper {
  display: initial;
}
</style>
